import { Component, OnInit } from '@angular/core';
import { Company } from '../company';
import { CompanyService } from '../company.service';

// Introduced with Angular 4, the async pipe provides a way for us to bind observables directly into our
// templates without manually subscribing in our components.
// we importing observable in this template for this
import { Observable } from 'rxjs/observable';


@Component({
  selector: 'fbc-company-list',
  templateUrl: './company-list.component.html',
  styleUrls: ['./company-list.component.scss']
})
export class CompanyListComponent implements OnInit {
 // In Angular 4
// update the type of the companies collection to be an Observable.
// Note the $ suffix on the property name. This is a convention used to label observable properties.

public companies$: Observable <Company[]>;

  //  with angular 2
  // companies: Company[];

  constructor(private companyService: CompanyService) { }

  ngOnInit() {
    this.getCompanies();
  }

// In Angular 4
// update the getCompanies() method to remove the subscription and pass the observable directly to companies

getCompanies() {
this.companies$ = this.companyService.getCompanies();
}

deleteCompany(companyId: number) {
  this.companyService.deleteCompany(companyId)
    .subscribe(() => this.getCompanies());
}


// In angular2
  // getCompanies() {
  //   this.companyService.getCompanies()
  //     .subscribe(companies => this.companies = companies);
  // }

// At last
//   update the template to use the async pipe.
//   <tr *ngFor="let company of companies$ | async">

}



