import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Company } from './company';
import { HttpClient, HttpHeaders } from '@angular/common/http';



@Injectable()
export class CompanyService {
    API_BASE = 'http://firebootcamp-crm-api.azurewebsites.net/api';

  constructor(private httpClient: HttpClient) { }

  getCompanies(): Observable<Company[]> {
    return this.httpClient.get(`${this.API_BASE}/company`)
    .catch(this.errorHandler);
  }

  deleteCompany(companyId: number): Observable<Company> {
    return this.httpClient.delete<Company>(`${this.API_BASE}/company/${companyId}`);
  }

  addCompany(company: Company) {
    return this.httpClient.post(`${this.API_BASE}/company`, company, { headers: new HttpHeaders().set('content-type', 'application/json') }
    )
      .catch(this.errorHandler);
  }

  getCompany(companyId: number) {
    return this.httpClient.get(`${this.API_BASE}/company/${companyId}`)
      .catch(this.errorHandler);
  }

  updateCompany(company: Company): Observable<Company> {
    return this.httpClient.put(
      `${this.API_BASE}/company/${company.id}`, company,
      { headers: new HttpHeaders().set('content-type', 'application/json') }
    )
      .catch(this.errorHandler);
  }

private errorHandler(error) {
console.error('Custom Error!!!', error);
return Observable.throw(error);
}
}



